import React, { useState, useEffect, useRef } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';
import { userProfileService } from '../services/userProfileService';
import { lobbyService } from '../services/lobbyService';
import { cooperationGameService } from '../services/cooperationGameService';
import './GameLobby.css';

function GameLobby() {
  const { currentUser } = useAuth();
  const [lobbyId, setLobbyId] = useState('');
  const [players, setPlayers] = useState([]);
  const [isHost, setIsHost] = useState(false);
  const [error, setError] = useState('');
  const [copyStatus, setCopyStatus] = useState('');
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const currentLobbyId = useRef('');
  const currentPlayer = useRef(null);

  useEffect(() => {
    let unsubscribeFn = null;

    const initializeLobby = async () => {
      try {
        if (!currentUser) {
          setError('Please log in to join or create a lobby');
          setTimeout(() => navigate('/'), 2000);
          return;
        }

        // Get current user's nickname if logged in
        let playerName = '';
        try {
          const profile = await userProfileService.getUserProfile(currentUser.uid);
          if (profile?.nickname) {
            playerName = profile.nickname;
          }
        } catch (error) {
          console.error('Error fetching user profile:', error);
        }

        // Check for lobby ID in URL parameters
        const lobbyParam = searchParams.get('lobby');
        
        if (lobbyParam) {
          // Join existing lobby
          currentLobbyId.current = lobbyParam;
          setLobbyId(lobbyParam);
          setIsHost(false);
          
          // Set default name if no nickname
          if (!playerName) {
            playerName = 'Player 2';
          }
          
          // Use actual user ID instead of hardcoded value
          currentPlayer.current = { 
            id: currentUser.uid, 
            name: playerName,
            email: currentUser.email 
          };

          try {
            await lobbyService.joinLobby(lobbyParam, currentPlayer.current);
            // Rely on subscription for player list
          } catch (error) {
            console.error('Error joining lobby:', error);
            setError(error.message || 'Failed to join lobby');
            setTimeout(() => navigate('/'), 2000);
            return; // Stop execution if join fails
          }
        } else { // This 'else' now correctly corresponds to 'if (lobbyParam)'
          // Create new lobby
          const newLobbyId = Math.random().toString(36).substring(2, 8).toUpperCase();
          currentLobbyId.current = newLobbyId;
          setLobbyId(newLobbyId);
          setIsHost(true);
          
          // Set default name if no nickname
          if (!playerName) {
            playerName = 'Player 1';
          }
          
          // Use actual user ID instead of hardcoded value
          currentPlayer.current = { 
            id: currentUser.uid, 
            name: playerName,
            email: currentUser.email 
          };

          try {
            await lobbyService.createLobby(newLobbyId, currentPlayer.current);
            // Rely on subscription for player list
          } catch (error) {
            console.error('Error creating lobby:', error);
            setError(error.message || 'Failed to create lobby');
            setTimeout(() => navigate('/'), 2000);
            return; // Stop execution if create fails
          }
        } // End of if/else for join/create

        // Subscribe to lobby changes (this should now use the correct lobbyId)
        unsubscribeFn = lobbyService.subscribeToLobby(currentLobbyId.current, (lobbyData) => {
          if (lobbyData) {
            // Update players list with all players from the lobby
            const updatedPlayers = lobbyData.players || [];
            setPlayers(updatedPlayers);
            
            if (lobbyData.status === 'playing') {
              navigate('/game/population/cooperation/play', { 
                state: { lobbyId: currentLobbyId.current, players: updatedPlayers } 
              });
            }
          } else {
            setError('Lobby not found or has been closed');
            setTimeout(() => navigate('/'), 2000);
          }
        });
      } catch (error) {
        console.error('Error initializing lobby:', error);
        setError('Failed to initialize lobby');
      }
    };

    initializeLobby();

    // Cleanup subscription when component unmounts
    return () => {
      if (unsubscribeFn) {
        unsubscribeFn();
      }
      if (currentLobbyId.current && currentPlayer.current) {
        lobbyService.leaveLobby(currentLobbyId.current, currentPlayer.current.id);
      }
    };
  }, [searchParams, currentUser, navigate]);

  const copyLink = async () => {
    const baseUrl = window.location.origin;
    const gameLink = `${baseUrl}/#/game/population/cooperation?lobby=${encodeURIComponent(currentLobbyId.current)}`;
    
    try {
      await navigator.clipboard.writeText(gameLink);
      setCopyStatus('Copied!');
      setTimeout(() => {
        setCopyStatus('');
      }, 2000);
    } catch (err) {
      const textArea = document.createElement('textarea');
      textArea.value = gameLink;
      document.body.appendChild(textArea);
      textArea.select();
      try {
        document.execCommand('copy');
        setCopyStatus('Copied!');
        setTimeout(() => {
          setCopyStatus('');
        }, 2000);
      } catch (err) {
        setCopyStatus('Failed to copy');
        console.error('Failed to copy text: ', err);
      }
      document.body.removeChild(textArea);
    }
  };

  const startGame = async () => {
    if (!isHost) return;
    if (players.length < 2) {
      setError('Waiting for another player to join...');
      return;
    }
    try {
      setError('');
      
      // 1. Initialize game state in Firebase (this service function will now fetch/select countries)
      await cooperationGameService.initializeGameState(
        currentLobbyId.current, 
        players
        // No longer passing selectedCountries from here
      );

      // 2. Update lobby status to 'playing' (triggers navigation)
      await lobbyService.startGame(currentLobbyId.current);

    } catch (error) {
      console.error('Error starting game:', error);
      setError(error.message || 'Failed to start game');
    }
  };

  return (
    <div className="game-lobby">
      <h2>Game Lobby</h2>
      <div className="lobby-info">
        <p>Lobby ID: <strong>{lobbyId}</strong></p>
        {isHost && (
          <button 
            className={`button button-secondary copy-button ${copyStatus ? 'copied' : ''}`}
            onClick={copyLink}
          >
            {copyStatus || 'Copy Invite Link'}
          </button>
        )}
      </div>

      <div className="players-list">
        <h3>Players in Lobby:</h3>
        <ul>
          {players.map((player) => (
            <li key={player.id} className={player.id === currentPlayer.current?.id ? 'current-player' : ''}>
              {player.name}
            </li>
          ))}
        </ul>
      </div>

      {error && <p className="error">{error}</p>}

      <div className="lobby-actions">
        {isHost && (
          <button className="button button-primary" onClick={startGame}>
            Start Game
          </button>
        )}
        <button 
          className="button button-secondary" 
          onClick={() => navigate('/')}
        >
          Cancel
        </button>
      </div>
    </div>
  );
}

export default GameLobby; 