import React, { useState, useEffect } from 'react';
import { getLeaderboard } from '../api/leaderboardApi';
import { formatDate } from '../utils/dateUtils';
import './LeaderboardPage.css';

function LeaderboardPage() {
  const [classicLeaderboard, setClassicLeaderboard] = useState([]);
  const [cooperationLeaderboard, setCooperationLeaderboard] = useState([]);

  useEffect(() => {
    const loadLeaderboards = () => {
      const classicData = getLeaderboard('classic_population');
      const cooperationData = getLeaderboard('cooperation_population');
      setClassicLeaderboard(classicData);
      setCooperationLeaderboard(cooperationData);
    };
    loadLeaderboards();
  }, []); // Fetch only once on component mount

  const renderLeaderboardTable = (title, data) => (
    <div className="leaderboard-section">
      <h3>{title}</h3>
      {data.length > 0 ? (
        <div className="leaderboard-table">
          <table>
            <thead>
              <tr>
                <th>Rank</th>
                <th>Player</th>
                <th>Score</th>
                <th>Date</th>
              </tr>
            </thead>
            <tbody>
              {data.map((entry, index) => (
                <tr key={entry.id}>
                  <td>{index + 1}</td>
                  <td>{entry.playerName}</td>
                  <td>{entry.score}</td>
                  <td>{formatDate(entry.date)}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        <p>No entries yet for this mode.</p>
      )}
    </div>
  );

  return (
    <div className="leaderboard-page">
      <h2>Leaderboard</h2>
      
      {renderLeaderboardTable('Classic Population', classicLeaderboard)}
      {renderLeaderboardTable('Cooperation Population', cooperationLeaderboard)}

    </div>
  );
}

export default LeaderboardPage; 