import { realtimeDb } from '../firebase';
import { ref, set, update, remove, get, onValue, off } from 'firebase/database';

const LOBBIES_PATH = 'lobbies';

class LobbyService {
  constructor() {
    this.lobbiesRef = ref(realtimeDb, LOBBIES_PATH);
  }

  // Create a new lobby
  async createLobby(lobbyId, player) {
    const lobbyRef = ref(realtimeDb, `${LOBBIES_PATH}/${lobbyId}`);
    await set(lobbyRef, {
      players: [player],
      status: 'waiting',
      createdAt: Date.now(),
      lastUpdated: Date.now()
    });
    return lobbyId;
  }

  // Join an existing lobby
  async joinLobby(lobbyId, player) {
    const lobbyRef = ref(realtimeDb, `${LOBBIES_PATH}/${lobbyId}`);
    const snapshot = await get(lobbyRef);
    const lobbyData = snapshot.val();
    
    if (lobbyData) {
      const currentPlayers = lobbyData.players || [];
      await update(lobbyRef, {
        players: [...currentPlayers, player],
        lastUpdated: Date.now()
      });
    } else {
      throw new Error('Lobby not found');
    }
  }

  // Leave a lobby
  async leaveLobby(lobbyId, playerId) {
    const lobbyRef = ref(realtimeDb, `${LOBBIES_PATH}/${lobbyId}`);
    const snapshot = await get(lobbyRef);
    const lobbyData = snapshot.val();
    
    if (lobbyData) {
      const currentPlayers = lobbyData.players || [];
      const updatedPlayers = currentPlayers.filter(p => p.id !== playerId);
      
      if (updatedPlayers.length === 0) {
        // If no players left, delete the lobby
        await remove(lobbyRef);
      } else {
        // Otherwise update the players list
        await update(lobbyRef, {
          players: updatedPlayers,
          lastUpdated: Date.now()
        });
      }
    }
  }

  // Subscribe to lobby changes
  subscribeToLobby(lobbyId, callback) {
    const lobbyRef = ref(realtimeDb, `${LOBBIES_PATH}/${lobbyId}`);
    onValue(lobbyRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        callback(data);
      } else {
        callback(null);
      }
    });

    // Return unsubscribe function
    return () => off(lobbyRef);
  }

  // Start the game
  async startGame(lobbyId) {
    const lobbyRef = ref(realtimeDb, `${LOBBIES_PATH}/${lobbyId}`);
    await update(lobbyRef, {
      status: 'playing',
      startedAt: Date.now(),
      lastUpdated: Date.now()
    });
  }

  // End the game
  async endGame(lobbyId) {
    const lobbyRef = ref(realtimeDb, `${LOBBIES_PATH}/${lobbyId}`);
    await update(lobbyRef, {
      status: 'completed',
      endedAt: Date.now(),
      lastUpdated: Date.now()
    });
  }

  // Clean up lobby
  async cleanupLobby(lobbyId) {
    const lobbyRef = ref(realtimeDb, `${LOBBIES_PATH}/${lobbyId}`);
    await remove(lobbyRef);
  }
}

export const lobbyService = new LobbyService(); 