// src/pages/ClassicGamePage.js
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { fetchCountries } from '../api/countriesApi';
import CountryCard from '../components/CountryCard';
import './GamePage.css'; // Reuse some styles for now, can create ClassicGamePage.css later

function ClassicGamePage() {
  const [countriesToPick, setCountriesToPick] = useState([]);
  const [sortedCountries, setSortedCountries] = useState([]);
  const [currentCountry, setCurrentCountry] = useState(null);
  const [score, setScore] = useState(0);
  const [gameStatus, setGameStatus] = useState('loading'); // loading, picking, placing, ended
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const loadGame = async () => {
      setIsLoading(true);
      setError(null);
      try {
        const fetchedCountries = await fetchCountries();
        const shuffled = fetchedCountries.sort(() => 0.5 - Math.random());

        // Start the game with the first country already sorted
        if (shuffled.length >= 2) {
          const initialSortedCountry = shuffled[0];
          const firstCountryToPlace = shuffled[1];
          
          setSortedCountries([initialSortedCountry]); // Start with the first country placed
          setCurrentCountry(firstCountryToPlace); // Set the second country as the one to place
          setCountriesToPick(shuffled.slice(2)); // Remaining countries start from the third one
          setScore(1); // Start score at 1 since one country is already placed
          setGameStatus('placing');
        } else if (shuffled.length === 1) {
           // Handle case with only one country - technically game over?
           setSortedCountries([shuffled[0]]);
           setCurrentCountry(null);
           setCountriesToPick([]);
           setScore(0);
           setGameStatus('ended');
           // Maybe navigate immediately?
           navigate('/gameover', { 
             state: { score: 0, message: 'Only one country found, nothing to sort!', mode: 'classic_population' },
             replace: true 
           });
        } else {
          setError("Not enough countries found to start the game.");
          setGameStatus('ended');
        }
      } catch (err) {
        console.error("Failed to load countries:", err);
        setError(err.message || "Failed to load game data.");
        setGameStatus('ended');
      } finally {
        setIsLoading(false);
      }
    };
    loadGame();
  }, [navigate]);

  const pickNextCountry = () => {
    if (countriesToPick.length > 0) {
      const nextCountry = countriesToPick[0];
      setCurrentCountry(nextCountry);
      setCountriesToPick(countriesToPick.slice(1));
      setGameStatus('placing');
    } else {
      // All countries sorted correctly!
      setGameStatus('ended');
      navigate('/gameover', { 
        state: { 
          score: score, 
          message: 'Congratulations! You sorted all countries!',
          mode: 'classic_population'
        },
        replace: true 
      });
    }
  };

  const handlePlaceCountry = (index) => {
    if (!currentCountry || gameStatus !== 'placing') return;

    const countryToPlace = currentCountry;
    const newSortedCountries = [...sortedCountries];
    newSortedCountries.splice(index, 0, countryToPlace);

    // Check if the new placement is correct based on population (Lowest to Highest)
    let isCorrect = true;
    if (index > 0) { // Check predecessor
      // Population: Lower is better (comes first)
      isCorrect = isCorrect && newSortedCountries[index - 1].population <= countryToPlace.population;
    }
    if (index < newSortedCountries.length - 1) { // Check successor
      isCorrect = isCorrect && countryToPlace.population <= newSortedCountries[index + 1].population;
    }

    if (isCorrect) {
      setSortedCountries(newSortedCountries);
      setScore(score + 1);
      setCurrentCountry(null);
      pickNextCountry();
    } else {
      // Capture state *before* navigating
      const finalSortedList = [...sortedCountries]; // Copy the list before incorrect placement
      const incorrectCountry = countryToPlace;
      const attemptedIndex = index;

      setGameStatus('ended');
      navigate('/gameover', {
        state: {
          score: score, // Score before the failed attempt
          message: `Incorrect placement. Game Over!`,
          mode: 'classic_population',
          // Add final state details
          finalSortedList: finalSortedList,
          incorrectCountry: incorrectCountry,
          attemptedIndex: attemptedIndex
        },
        replace: true
      });
    }
  };

  if (isLoading) {
    return <div className="game-page">Loading Game...</div>;
  }

  if (error) {
    return <div className="game-page">Error: {error} <button onClick={() => window.location.reload()}>Retry</button></div>;
  }

  if (gameStatus === 'ended' && !isLoading) {
     // Should have navigated away, but show a fallback just in case
     return <div className="game-page">Game Over! Final Score: {score}</div>;
  }

  return (
    <div className="game-page">
      <h2>Classic Mode - Sort by Population (Lowest to Highest)</h2>
      <p>Score: {score-1}</p>
      

      <div className="sorted-countries-container">
        
        <div className="sorted-countries">
          {currentCountry && gameStatus === 'placing' && (
            <div
              className="insertion-point"
              onClick={() => handlePlaceCountry(0)}
              title={`Place ${currentCountry.name} at the beginning`}
            >
              +
            </div>
          )}
          {sortedCountries.map((country, index) => (
            <React.Fragment key={`fragment-${country.id}`}>
              <CountryCard
                country={country}
                isClickable={true}
                mode="population"
              />
              {currentCountry && gameStatus === 'placing' && (
                <div
                  className="insertion-point"
                  onClick={() => handlePlaceCountry(index + 1)}
                  title={`Place ${currentCountry.name} after ${country.name}`}
                >
                  +
                </div>
              )}
            </React.Fragment>
          ))}
        </div>
      </div>

      {gameStatus === 'placing' && currentCountry && (
        <div className="current-country">
          <h3>Place this Country:</h3>
          <div
            className="current-country-wrapper"
            title={`${currentCountry.name} - place it in the list above`}
          >
            <CountryCard
              country={currentCountry}
              isClickable={false}
              mode="population"
            />
          </div>
        </div>
      )}
       {/* Optionally show next few countries to pick later */}
       {/* <div className="remaining-countries"> ... </div> */}

    </div>
  );
}

export default ClassicGamePage; 